.sub-list {
  padding: 16px 46.5px;
  display: none;
  &--opened {
    display: block;
  }
  &__button {
    padding: 5px 27px;
    display: flex;
    align-items: center;
    width: 100%;
    border: none;
    text-align: left;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    cursor: pointer;
    color: #fff;
    background-color: #1E2D3D;
    &::before {
      content: "";
      display: block;
      width: 6px;
      height: 8.5px;
      background-size: 6px 8.5px;
      margin-right: 13.5px;
      background-image: url("../img/button-arrow--right.svg");
    }

    &--active {
      &::before {
        background-image: url("../img/button-arrow--down.svg");
        margin-right: 11px;
        width: 8.5px;
        height: 6px;
        background-size: 8.5px 6px;
      }
    }
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: $grey;
    &::before {
      content: "";
      display: block;
      width: 16.2px;
      height: 14.6px;
      background-size: 16.2px 14.6px;
      margin-right: 8px;
    }
    &--education {
      &::before {
        background-image: url("../img/icon-education.svg");
      }
    }
    &--professionals {
      &::before {
        background-image: url("../img/icon-professionals.svg");
      }
    }
    &--interests {
      &::before {
        background-image: url("../img/icon-interests.svg");
      }
    }
    &--mail {
      &::before {
        background-image: url("../img/icon-mail.svg");
      }
    }
    &--games {
      &::before {
        background-image: url("../img/icon-games.svg");
      }
    }
    &--music {
      &::before {
        background-image: url("../img/icon-music.svg");
      }
    }
    &--films {
      &::before {
        background-image: url("../img/icon-films.svg");
      }
    }
  }
  &__icon {
    margin-right: 9px;
    &--hobbies {
      fill: #43D9AD;
    }
    &--contacts {
      fill: #3A49A4;
    }
  }
}

@media (min-width: $desktop-width) {
  .sub-list {
    &__button {
      padding: 14px 15px;
      background-color: transparent;
      &--active {
        border-bottom: 1px solid $border-color;
      }
    }
    padding: 18px 0 16px 34px;
  }
}
